<template lang="pug">
div.bg
    div.feedback
        p.feedback__title ?
        div.feedback__item
            textarea(
            style="height:2rem;color:black" 
            class="feedback__item-textarea" 
            placeholder=": bug, "
            v-model = "title"
            )
            p.feedback__item-count  {{titleLeaveLength}} 
        div.feedback__item
            textarea(
            style="height:9rem;color:black" 
            class="feedback__item-textarea" 
            placeholder=""
            v-model = "content"
            )
            p.feedback__item-count  {{contentLeaveLength}} 
        p.feedback__explain  </p>
        
        div.feedback__btn
                button(@click="saveFeedBack" class="btn btn-primary" v-bind:disabled="enabled") 
</template>
<script>
import {mapState} from 'vuex'
export default {
    name: "FeedBack",
    data(){
        return{
            titleLength:20,
            contentLength: 100,
            title: '',
            content: ''
        }
    },
    computed: {
            titleLeaveLength() {
                if (this.titleLength > this.title.length) {
                    return this.titleLength - this.title.length
                } else {
                    this.title = this.title.substr(0, this.titleLength)
                    return 0
                }
            },
            contentLeaveLength() {
                if (this.contentLength > this.content.length) {
                    return this.contentLength - this.content.length
                } else {
                    this.content = this.content.substr(0, this.contentLength)
                    return 0
                }
            },
            enabled() {
                return !this.title||!this.content
            },
            ...mapState('user', ['userInfo'])
        },
    methods:{
        saveFeedBack(){
            if (!this.title && !this.content) {
                    return
                }
                this.$store.dispatch('user/feedback', {
                    tag: this.title,
                    content: this.content,
                    userId: this.userInfo.userId
                });
        }
    }
}
</script>
<style lang="stylus">
    @import "./../assets/stylus/common.stylus"
    $color=white
    .feedback
        width 90%
        margin 0 auto 
        padding-top 10%
        &__title
            color: $color
            font-size .9rem
        &__item
            margin: 1rem 0;
            &-textarea
                widthHeightLineHeightFontSize(100%, 1rem,1rem,.9rem)
                color $color
                border 1px solid $color
                border-radius(10px)
                resize none
                line-height 2rem
            &-count
                text-align right 
                font-size .8rem
        &__explain
            margin-bottom 4rem
            color $color
            font-size .9rem
            line-height 1.5rem
        &__btn
            width 100%
            height 2rem
</style>


