import axios from 'axios'
import {ChargerError, ERR_TYPE} from './chargerError'
import wx from 'weixin-js-sdk'
import isWX from './isWX.js'

export function getLocation(){
    return new Promise((resolve,reject)=>{
        wx.getLocation({
        type: 'wgs84',
        success:function(res){
            // let latitude = res.latitude; // 90 ~ -90
            // let longitude = res.longitude; // 180 ~ -180
            resolve(res)
        },
        fail:(res)=>{
            reject(new ChargerError(ERR_TYPE.PAY_ERR, ""))
        },
     })
   })
}


export function wxPay(payParam){
    return new Promise((resolve,reject)=>{
        wx.chooseWXPay({
            nonceStr: payParam.nonceStr, //  32 
            package: payParam.package, // prepay_id : prepay_id=\*\*\*
            paySign: payParam.paySign, //
            signType: payParam.signType, //  'SHA1',  'MD5'
            timestamp: payParam.timestamp, // ,   jssdk  timestamp   timestamp  S 
            success: (res)=>{
                resolve(res)
            },
            fail:(res)=>{
                reject(new ChargerError(ERR_TYPE.PAY_ERR, ""))
            },
            cancel:(res)=>{
                reject(new ChargerError(ERR_TYPE.PAY_CANCEL, ""))
            }
        })
    })
}

export default async function wxInit(){
    if(isWX()){
        let resp = await axios.get('/wechat/jssdk')
        let config = resp.data;
        wx.config(config);
        return await _parseWX(wx);
    }else{
        throw new ChargerError(ERR_TYPE.VALID_AGENT);
    }
}

function _parseWX(wxHandle){
    return new Promise((resolve,reject)=>{
        wxHandle.ready(function(){
            resolve(wxHandle);
        })

        wxHandle.error(function(e){
            reject(e)
        })
    });
}

